<template>
<form class="col s8">
<forminput label="Как вас зовут?"
            :type="'text'"
            :id="'name'"
            :mask="'ЯЯЯЯЯЯЯЯЯЯЯЯ'"
            :class="'input-field col s6'"
            @inputData="getName"/>

<forminput label="Телефон"
            :id="'phone'"
            :type="'tel'"
            :mask="'+7(9##)-###-##-##'" 
            :class="'input-field col s6'"
            @inputData="getPhone"
            @click="phone='+7(9666'"/>

<formtextarea label="Укажите комментарий"
              :id="'text'"
              :class="'input-field col s6'"
              @inputData="getText"/>

<formdatepicker label="Когда Вам удобно принять звонок?"
                :id="'date'"
                :class="'input-field col s6'"
                :modal="modal"
                @inputData="getDate"/>

<formtimepicker v-if="date" label="Время звонка"
                :id="'time'"
                :modal="modal"
                @inputData="getTime"
                :class="'input-field col s6'"/>

<btn label="оставить заявку"
      :ready="formCompleted"
      :class="{'disabled':!formCompleted}"
      class="align"
      @sendForm="sendForm"/> 

</form>
</template>

<script>
import btn from './btn.vue'
import formtimepicker from './form/formtimepicker'
import formdatepicker from './form/formdatepicker'
import formtextarea from './form/formtextarea.vue'
import forminput from'./form/forminput.vue'



export default {
  name: 'callbackform',
   data() {
          return {
            name: '', 
            phone: '',
            text: '',
            date: '',
            time: '',
            stepOne: false,
            stepTwo: false
            // ready: false
          }
        },
        components:{
          forminput,
          formtextarea,
          formdatepicker,
          formtimepicker,
          btn,
        },
        watch:{},
        computed:{
          formCompleted(){
            return this.stepOne && this.stepTwo
          }
        },
        props:{
          modal: Boolean
        },
        methods:{
          getName(inputData, ready){
            this.name = inputData
            this.stepOne = ready
          },
          getPhone(inputData, ready){
            this.phone = inputData
            this.stepTwo = ready

          },
          getText(inputData){
            this.text = inputData
          },
          getDate(inputData){
            this.date = inputData 
          },
          getTime(inputData){
            this.time = inputData 
          },
          sendForm(){
              this.$axios.post('lead/create', {
                      name: this.name, 
                      phone: this.phone,
                      text: this.text, 
                      date: this.date,
                      time: this.time})
    } 
}
}
</script>

