<template>
  <div class="body">
    <div class="cont top_side">
      <div class="col_l add_label z-depth-3">
        <h4>новый проект</h4>
        <forminput
          label="Заголовок"
          :id="'title'"
          :type="'text'"
          :class="'input-field col s6'"
          @inputData="getTitle"
        />

        <forminput
          label="Подзаголовок"
          :id="'subTitle'"
          :type="'text'"
          :class="'input-field col s6'"
          @inputData="getSubTitle"
        />

        <div class="input-field col s6">
          <select ref="selectDate" v-model="date">
            <option value="" disabled selected>Выберите дату</option>
            <option :value="`1-ый квартал ${thisYear} г.`">
              1-ый квартал {{ thisYear }} г.
            </option>
            <option :value="`2-ой квартал ${thisYear} г.`">
              2-ой квартал {{ thisYear }} г.
            </option>
            <option :value="`3-ий квартал ${thisYear} г.`">
              3-ий квартал {{ thisYear }} г.
            </option>
            <option :value="`4-ый квартал ${thisYear} г.`">
              4-ый квартал {{ thisYear }} г.
            </option>
          </select>
          <!-- <label>Materialize Select</label> -->
        </div>

        <div class="file-field input-field col s6">
          <div class="btn">
            <span><i class="material-icons">attach_file</i></span>
            <input
              type="file"
              ref="fileInput"
              accept="image/x-png,image/gif,image/jpeg"
              @change="getFile"
            />
          </div>
          <div class="file-path-wrapper">
            <input
              class="file-path validate"
              type="text"
              placeholder="Загрузить файл"
            />
          </div>
        </div>
      </div>
      <div class="col_r">
        <project :date="date" :title="title" :subtitle="subTitle" :src="src" />
        <btn class="btnSave" @sendForm="send" :label="'опубликовать'" />
        <!-- :class="{'disabled': !ready}"/> -->
      </div>
    </div>
    <!-- <secprojects data-scroll-section class="projects" :title="'последние проекты'"/> -->

    <div class="cont bottom_side">
      <h2>Наши проекты</h2>
      <projectComp
        v-for="(project, i) in data"
        :key="project._id"
        :i="i"
        :admin="admin"
        :project="project"
        :reload="getAll"
      />
    </div>
  </div>
</template>

<script>
import M from "materialize-css";
import forminput from "../components/form/forminput";
import btn from "../components/btn";
import projectComp from "../components/projectComp";
import project from "../components/project";

M.AutoInit();

export default {
  name: "NewProject",
  data() {
    return {
      admin: false,
      ready: false,
      src: "",
      loaded: false,
      data: [],
      file: "",
      title: "Заголовок",
      subTitle: "Подзаголовок",
      date: `1-ый квартал ${new Date().getFullYear()} г.`,
    };
  },
  components: {
    forminput,
    btn,
    projectComp,
    project,
  },
  computed: {
    thisYear() {
      return new Date().getFullYear();
    },
  },
  watch: {},
  mounted() {
    M.FormSelect.init(this.$refs.selectDate, {});
  },
  created() {
    this.getAll();
    this.login();
  },
  methods: {
    onSwiper(swiper) {
      console.log(swiper);
    },
    onSlideChange() {
      console.log("slide change");
    },
    getSubTitle(inputData) {
      this.subTitle = inputData;
    },
    getTitle(inputData) {
      this.title = inputData;
    },
    async getAll() {
      const data = await this.$axios.get("project/all");
      this.data = data.data;
      this.loaded = true;
    },
    async login() {
      await this.$axios.post("user/login", { username: "admin", password: 1 });
      const isAuth = await this.$axios.post("user/testAut");
      if (isAuth.data) {
        this.admin = true;
      }
    },
    getFile() {
      this.file = this.$refs.fileInput.files[0];
      this.src = URL.createObjectURL(this.file);
    },
    async send() {
      const formData = new FormData();
      formData.append("file", this.file);
      formData.append("title", this.title);
      formData.append("subTitle", this.subTitle);
      formData.append("date", this.date);

      await this.$axios.post("project/new_project", formData);
      this.stc = null;
      this.getAll();
    },
  },
};
</script>
